.button {
  box-sizing: border-box;
  color: #0078db;
  cursor: pointer;
  z-index: 1;
  background-color: #0000;
  border: 2px solid #0078db;
  border-radius: 2px;
  width: max-content;
  padding: 14px 28px;
  font-weight: 700;
  text-decoration: none;
  transition: all .3s;
  display: inline-block;
  position: relative;
  overflow: hidden;
}

.button:before {
  content: "";
  z-index: -1;
  background-color: #0078db;
  width: 100%;
  height: 100%;
  transition: all .3s;
  position: absolute;
  top: 0;
  left: 0;
  transform: translateX(-100%);
}

.button:hover {
  color: #fff;
}

.button:hover:before {
  transform: translateX(0);
}

.button--light {
  background-color: #fff;
  border-color: #fff;
}

.spoller-block--init .spoller-block__title {
  cursor: pointer;
}

.spoller-block--init .spoller-block__title:after, .spoller-block--init .spoller-block__title:before {
  content: "";
  background-color: #0078db;
  width: 20px;
  height: 1px;
  transition: transform .3s;
  position: absolute;
  top: 50%;
  right: 0;
}

.spoller-block--init .spoller-block__title:after {
  transform: rotate(-90deg);
}

.spoller-block--init .spoller-block__title--active:after {
  transform: rotate(0);
}

.page {
  color: #000;
  scroll-behavior: smooth;
  margin: 0;
  padding: 0;
  font-family: DM Sans, sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 130%;
}

.page__body {
  background-image: url("header-background.648bdabb.svg");
  background-position: 100% 0;
  background-repeat: no-repeat;
  background-size: 100%;
  margin: 0;
}

@media (width >= 1024px) {
  .page__body {
    background-size: 55%;
  }
}

.page__body--with-menu {
  overflow: hidden;
}

.page__menu {
  transition: transform .3s;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  transform: translateX(-100%);
}

.page__menu:target {
  transform: translateX(0);
}

.page__section {
  padding: 30px 0;
}

@media (width >= 1024px) {
  .page__section {
    margin: 0 30px;
    padding: 60px 0;
  }
}

.page__section-title {
  text-rendering: optimizelegibility;
  letter-spacing: -1px;
  -webkit-font-smoothing: antialiased;
  margin: 0 0 32px;
  padding: 0;
  font-size: 2rem;
  font-weight: 400;
  line-height: 1;
}

@media (width >= 640px) {
  .page__section-title {
    font-size: 2.625rem;
  }
}

.page__section-title--white {
  color: #fff;
}

.header__content {
  box-sizing: border-box;
  flex-direction: column;
  width: 100%;
  min-height: 100vh;
  padding: 26px 0 40px;
  display: flex;
}

@media (width >= 1024px) and (height <= 900px) {
  .header__content {
    min-height: 100vh;
  }
}

@media (width >= 1024px) and (height >= 900px) {
  .header__content {
    min-height: 900px;
  }
}

.header__top {
  --icon-width: 18px;
  grid-template-columns: 1fr var(--icon-width) var(--icon-width);
  place-items: center start;
  column-gap: 30px;
  display: grid;
}

@media (width >= 640px) {
  .header__top {
    --icon-width: 24px;
    column-gap: 40px;
  }
}

.header__bottom {
  flex-direction: column-reverse;
  margin: auto 0;
